import * as React from 'react';
import { StyleSheet, ScrollView, View, Text, FlatList, TouchableOpacity } from 'react-native';



import Background from '../../component/background/background';

import config from "../../config/config";
import { useAsync } from "react-async";
import axios from "axios";
import { Spinner } from '@ui-kitten/components';
import _ from 'lodash';
import { SearchContext } from '../../contexts/searchContext';
import ErrorModel from '../../component/modalError/modalError';
import Accordian from '../../component/accordian/accordian';
import { isEmpty } from 'lodash';
import { removeTag, removeBoldTag, removeGrayTag, removeSmallTag } from '../bookView/bookViewClass';
const headers = ["header1", "header2", "header3", "header4", "header5", "header6", "header7"]


const getSearchContent = async ({ booksIds, searchInput, type, tableInput }) => {
    const { data } = await axios.post(`${config.serverUrl}/book/search/`, {
        "content": searchInput,
        "type": !isEmpty(tableInput) ? type || 'exact' : 'exact',
        size: 50,
        table:tableInput,
        "booksIds": booksIds
    });
    return Promise.all(data.map(async verse => {

        return { ...verse }
    }))
}


const SearchView = ({ navigation, route }) => {
    const { searchInput, searchType,tableInput } = React.useContext(SearchContext);

    const { data, error, isPending } = useAsync({ promiseFn: getSearchContent,tableInput, booksIds: route.params.booksIds, type: searchType, searchInput })
    const [showErrorModel, setShowErrorModel] = React.useState(false)
    React.useEffect(() => {
        if (error) {
            setShowErrorModel(true);
        }
    }, [error])
    return (
        <Background>
            <ErrorModel errorMsg={"     "} errorTitle={''} visible={showErrorModel} setVisible={setShowErrorModel} />

            <View style={styles.page}>
                {!isPending && data && data.length ?
                    <FlatList
                        keyExtractor={item => item.id}
                        style={styles.view} data={data} renderItem={({ item, index }) => {
                            let content = item.content.match(/(?:<(\w+)[^>]*>(?:[\w+]+(?:(?!<).*?)<\/\1>?)[^\s\w]?|[^\s]+)/g);
                            if (item.highlight && item.highlight[0]) {
                                content = item.highlight[0].match(/(?:<(\w+)[^>]*>(?:[\w+]+(?:(?!<).*?)<\/\1>?)[^\s\w]?|[^\s]+)/g);
                            }

                            let grayText = false;
                            let boldText = false;
                            let smallText = false;
                            let header = `${item.groupName.replace('_','"')}, ${item.bookName.replace('_','"')}`;
                            headers.forEach(headersType => {
                                if(item[headersType]){

                                    header += `, ${item[headersType]}`
                                }
                            } 
                            )
                            
                            return (<Accordian initExpanded={true} header={header} >
                                <TouchableOpacity onPress={() => {
                                    navigation.push('Result', {selectedIndex:item.index, selectedBooks: [{ bookId: item.bookId }] })
                                }} style={styles.contentContainer}>
                                    <View style={styles.pasokContainer}>
                                        {item.verse ? <Text style={styles.pasok}>{item.verse} </Text> : <></>}
                                        {content.map(splitContent => {
                                            if (RegExp(`<\s*em[^>]*>(.*?)<\s*/\s*em>`).test(splitContent)) {
                                                return <><Text>{' '}</Text><Text style={styles.pasokContentMark}>{removeTag(' ' + splitContent.match(/<em>(.*?)<\/em>/g).map((val) => val.replace(/<\/?em>/g, ''))).trim()}</Text></>
                                            }
                                            if (RegExp(`<\s*/\s*em>(.*?)<\s*em[^>]*>`).test(splitContent)) {
                                                return <><Text>{' '}</Text><Text style={styles.pasokContentMark}>{removeTag(splitContent.match(/<\/em>(.*?)<em>/g).map((val) => val.replace(/<\/?em>/g, ''))).trim() }</Text></>
                                            }
                                            if (RegExp(`<\s*[^>]*>(.*?)`).test(splitContent)) {
                                                grayText = true;
                                            }
                                            if (RegExp(`(.*?)<\s*/\s*>`).test(splitContent)) {
                                                grayText = false;
                                                return <Text style={styles.pasokContentGray}> {removeTag(splitContent)}</Text>
                                            }
                                            if (grayText) {
                                                return <Text style={styles.pasokContentGray}>{removeTag(splitContent)}</Text>
                                            }
                                            if (RegExp(`<\s*[^>]*>(.*?)`).test(splitContent)) {
                                                smallText = true;
                                            }
                                            if (RegExp(`(.*?)<\s*/\s*>`).test(splitContent)) {
                                                smallText = false;
                                                return <Text style={styles.pasokContentSmall}> {removeTag(splitContent)}</Text>
                                            }
                                            if (smallText) {
                                                return <Text style={styles.pasokContentSmall}> {removeTag(splitContent)}</Text>
                                            }
                                            if (RegExp(`<\s*[^>]*>(.*?)`).test(splitContent) || RegExp(`<\s*[^>]*>(.*?)`).test(splitContent)) {
                                                boldText = true;
                                            }
                                            if (RegExp(`(.*?)<\s*/\s*>`).test(splitContent) || RegExp(`(.*?)<\s*/\s*>`).test(splitContent)) {
                                                boldText = false;
                                                return <Text style={styles.pasokContentBold}> {removeBoldTag(splitContent)}</Text>
                                            }
                                            if (boldText) {
                                                return <Text style={styles.pasokContentBold}> {removeBoldTag(splitContent)}</Text>
                                            }


                                            return <Text style={styles.pasokContent}> {removeTag(splitContent)}</Text>
                                        }
                                        )}
                                    </View>
                                </TouchableOpacity>
                            </Accordian>)
                        }} />
                    :
                    <View style={styles.spinnerContainer}>
                        <Spinner />
                    </View>}
            </View>
        </Background>
    )
}

const styles = StyleSheet.create({
    contentContainer: {
        paddingHorizontal: 35,
        paddingVertical: 10,
        flex: 1,
        width: '100%'
    },
    pasokContentMark: {
        color: '#455253',
        fontFamily: "OpenSansHebrew",
        textAlign: 'right',
        backgroundColor: 'yellow',
        fontSize: 20,
    },
    pasokContentSmall: {
        color: '#455253',
        textAlign: 'right',
        fontSize: 14 ,
    },
    pasokContentBold: {
        color: '#455253',
        fontFamily: "OpenSansHebrewBold",
        textAlign: 'right',
        fontSize: 19,
        padding: 0
    },
    pasokContentGray: {
        color: '#CBD4D3',
        fontFamily: "OpenSansHebrew",
        textAlign: 'right',
        fontSize: 18,
    },
    pasok: {
        color: '#455253',
        fontFamily: "OpenSansHebrewBold",
        textAlign: 'right',
        fontSize: 18,
        padding: 0
    },
    pasokContent: {
        color: '#455253',
        fontFamily: "OpenSansHebrew",
        textAlign: 'right',
        fontSize: 20,
        padding: 0

    },
    pasokContainer: {
        flexWrap: 'wrap',
        flexDirection: 'row-reverse',
  

    },
    view: {
        width: '100%'
    },
    scroll: {
        flex: 1,
        width: '100%'
    },
    spinnerContainer: {
        flex: 1,
        width: '100%',
        justifyContent: 'center',
        alignItems: 'center'
    },
    page: {
        flex: 1,
        width: '100%',
        height: '100%',
        alignItems: 'center',
        alignContent: 'center',
    }
});

export default SearchView;
